import s from "./ProfileInfo.module.css";
import React from "react";
import {createField, Input, Textarea} from "../../commons/FormsControls/FormsControls";
import {reduxForm} from "redux-form";
import style from "../../commons/FormsControls/FormsControls.module.css";
import p from "./ProfileInfoDataForm.module.css"

const ProfileDataForm = ({handleSubmit, profile, error}) => {
    return (
        <form onSubmit={handleSubmit}>
            <div className={p.info}>
                <div>
                    <button className={p.button}>Save</button>
                </div>
                {error && <div className={style.formSummaryError}>
                    {error}
                </div>}
                <div className={p.flex}>
                    <b className={p.paddings}>Full name: </b> {createField('Full name', 'fullName', [], Input)}
                </div>
                <div className={p.flex}>
                    <b className={p.paddings}>About me: </b> {createField('About me', 'aboutMe', [], Textarea)}
                </div>
                <div>
                    <div className={p.contact}><b>Contacts:</b></div> {Object.keys(profile.contacts).map(key => {
                    return <div key={key} className={s.contacts}>
                        <b className={p.grid}>{key}: {createField(key, 'contacts.' + key, [], Input)}</b>
                    </div>
                })}
                </div>
                <div className={p.flex}>
                    <b className={p.paddings}>Looking for a job: </b> {createField('', 'lookingForAJob', [], Input, {type: 'checkbox'})}
                </div>
                <div className={p.flex}>
                    <b className={p.paddings}>A job description: </b> {createField('A job description', 'lookingForAJobDescription', [], Textarea)}
                </div>
            </div>
        </form>
    )
}

const ProfileDataFormReduxForm = reduxForm({form: 'edit-profile'})(ProfileDataForm)

export default ProfileDataFormReduxForm