import React, {useState} from "react";
import s from "./ProfileInfo.module.css";
import Preloader from "../../commons/Preloader/Preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import userPhoto from "../../../assets/images/user.png"
import ProfileDataFormReduxForm from "./ProfileDataForm";

const ProfileInfo = (props) => {
    let [editMode, setEditMode] = useState(false)

    if (!props.profile) {
        return <Preloader/>
    }

    const onSubmit = (formData) => {
        props.saveProfile(formData).then(() => {
            setEditMode(false)
        })
    }

    const mainPhotoSelected = (e) => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0])
        }
    }

    return (
        <div>
            <div className={s.avatarItem}>
                <img src={props.profile.photos.large || userPhoto}/>
                <div>
                    <div><b>{props.profile.fullName}</b></div>
                    <div><ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/></div>
                </div>
            </div>
            <div className={s.changePhoto}>
                {props.isOwner && <input onChange={mainPhotoSelected} type={"file"} name={"choose photo"} />}
            </div>

            {editMode
                ? <ProfileDataFormReduxForm initialValues={props.profile} profile={props.profile} onSubmit={onSubmit}/>
                : <ProfileData profile={props.profile} isOwner={props.isOwner} goToEditMode={() => setEditMode(true)}/>}

        </div>
    )
}

const ProfileData = ({profile, isOwner, goToEditMode}) => {
    return (
        <div className={s.info}>
            {isOwner && <div>
                <button className={s.button} onClick={goToEditMode}>edit</button>
            </div>}
            <div className={s.block}>
                <div className={s.paddings}>
                    <div><b>Looking for a job:</b></div> {profile.lookingForAJob ? "yes" : "no"}
                    {profile.lookingForAJob &&
                        <div className={s.paddings}>
                            <b>My professional skills:</b> {profile.lookingForAJobDescription}
                        </div>}
                </div>
                <div className={s.paddings}>
                    <div><b>About me:</b></div> {profile.aboutMe}
                </div>
                <div className={s.contacts}>
                    <b>Contacts:</b> {Object.keys(profile.contacts).map(key => {
                    return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]}/>
                })}
                </div>
            </div>
        </div>
    )
}
const Contact = ({contactTitle, contactValue}) => {
    return <div className={s.paddings + ' ' + s.contacts}><b>{contactTitle}: </b>{contactValue}</div>
}
export default ProfileInfo;

